.list__container{
    padding: 5rem;
}
.home__box{
    display: flex;
    column-gap: 3rem;
    margin-top: 2rem;
}
.home__box h3{
    font-size: var(--big-font-size);
    font-family: var(--second-font);
}
.home__box span{
    font-size: var(--small-font-size);
}
/* Navigation for mobile */
@media screen and (max-width: 768px) {
    .list__container{
        padding: 1rem 0;
    }

}
.icon{
    font-size: 20px;
    background: var(--gradient-color);
    -webkit-background-clip: text;
    color: transparent;
    position: relative;
}
.icon::after{
    content: '';
    position: absolute;
    width: 20px;
    height: 20px;
    background-color: var(--gradient-color);
    top: 0 ;
    right: -20px;

}
.process_flex{
    display: flex;
    gap: 1.5rem;
    flex-wrap: wrap;
}
.process_flex .list__content{
    box-shadow: 0 0 1px 0 rgba(8, 11, 14, 0.06), 0 6px 6px -1px rgba(8, 11, 14, 0.1);
    border: 1px solid #F7F8F9;
    padding: 2rem;
    max-width: 50%;
    border-radius: 12px;
}
@media screen and (max-width: 450px) {
    .process_flex .list__content{
        max-width: 100%;
        width: 100%;
    }

}


/* Parallax css */
.aboutwrapper{
    background-image: url(../../../assets/background-lg.png);
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: 100%;
}
.about-parallax{
    max-width: 1300px;
    margin: 0 auto;
    position: relative;
  z-index: 0;
  min-height: 75vh;
  border: 1px solid red;
}
.about-parallax > * {
    animation: parallax linear forwards;
    animation-timeline: view();
    animation-range: contain;
  }
  .hero {
  }
.bg-img{
    position: sticky;
    top: 0;
    right: 0;
  z-index: 3;
}
.hero__title{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 60vh;
    font-size: 72px;
    font-weight: 900;
}
.bg-grid{
    width: 100%;
    display: flex;
    justify-content: center;
    transform: translateY(-20vh);
}
.bg{
    width: 200px;
    
}
.bg-con{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: auto;
}
.about-parallax p{
    width: 100%;
    display: flex;
    justify-content: center;
    transform: translateY(-100px);
    font-size: 40px;
    font-weight: 300;
}
@keyframes parallax {
    to {
      transform: 
        translateY(calc(var(--parallax-speed) * 200px));
    }
  }
@media screen and (max-width: 1024px) {
    .about-parallax{
        margin: 0 3rem;
    }

}
@media screen and (max-width: 768px) {

    .about-parallax{
        margin: 0 1rem;
    }

}
@media screen and (max-width: 425px) {

    .hero__title{
        font-size: 32px;
        text-align: center;
    }
    .bg{
        width: 150px;
    }
    .about-parallax p{
        font-size: 20px;
        text-align: center;
    }

}