.profile__card{
    position: relative;
    max-width: 395px;
}
.profile__card-image img{
    border-radius: 5px;
    width: 100%;
    aspect-ratio: 4/5;
    object-fit: cover;
    border: 1px solid #d7cbcb96;
}
.profile__card-content{
    position: absolute;
    bottom: 0;
    left: 0;
    padding: 10px 15px;
    width: 100%;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    color: antiquewhite;
    background: rgb(2,0,36);
background: linear-gradient(0deg, rgba(2, 0, 36, .8) 0%, rgba(0, 0, 0, 0.3) 35%, rgba(0, 0, 0, 0) 100%);
}
.profile__top{
    display: flex;
    justify-content: space-between;
    position: absolute;
    top: 0;
    width: 100%;
    padding: 10px 15px;
}
.profile__desc h1{
    font-weight: 500;
    color: rgb(255, 255, 255);
}
.profile__desc h4{
    color: rgb(255, 255, 255);
    font-weight: 300;
    font-size: 12px;
}
.profile__btn{
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
}

.fa-phone{
    color: blue;
}
.fa-heart{
    color: red;
}
.fa-star{
    color: gold;
}
@media screen and (max-width: 768px) {
    .profile__card{
        width: 320px;
    }
}
@media screen and (max-width: 450px) {
    .profile__card{
        width: 100%;
    }
    .close{
        margin-top: 100px;
    }

}


.carousel-inner {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.carousel{
    height: 100%;
    width: 100%;
    background-color: white;
}
.carousel-item{
    margin-right: 0 !important;
    object-fit: contain;
    position: relative;
}
.modal{
    width: 100%;
    height: calc(100dvh - 70px);
    z-index: 5;
    display: block;
    position: absolute;
    top: 0;
    left: 0;
}

.carousel-item img {
    margin: 0 auto; /* this will align the image to center. */
    border-radius: 5px;
    box-shadow: 1px 1px 12px 1px rgba(0,0,0,0.15);
    width: auto; /* for those images which don't have a width specified, but has a max-height will be auto adjusted */
  }

  .close{
    position: absolute;
    z-index: 6;
    right: 0;
    font-size: 20px;
    padding: 5px 12px;
    margin-top: 5px;
    margin-right: 5px;
    cursor: pointer;
    background-color: rgba(255, 255, 255, 0.37);
    box-shadow: 2px 3px 10px 1px rgba(0,0,0,0.15);
    border-radius: 50%;
  }
.carousel{
    position: relative;
}
  .carousel-btn{
    cursor: pointer;
    position: absolute;
    padding: 10px 17px;
    background-color: rgba(255, 255, 255, 0.404);
    box-shadow: 2px 3px 10px 1px rgba(0,0,0,0.15);
    border-radius: 50%;
    top: 50%;
    transform: translateY(-50%);
  }
  .carousel-left{
    left: 10px;
  }
  .carousel-right{
    right: 10px;
  }
  .carousel-inner{
    width: 100%;
    margin: 0;
  }

  .userhome__container:has(.modal){
    max-height: 0;
    overflow: hidden;
  }
  .home-section:has(.profileview){
    padding: 0;
  }

  .carousel-dot{
    width: 100%;
    display: flex;
    justify-content: center;
    position: absolute;
    bottom: 40px;
    gap: 10px;
  }
  .dot-indicators{
    width: 30px;
    height: 5px;
    border-radius: 20px;
    background-color: #ffffff7c;
    transition-duration: 1s;
  }
  .dot-indicators.active{
    background-color: white;
  }